.wptelegram-pro-instant-post {
	min-width: 40%;
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	&__footer {
		margin-top: 2em;
		&--buttons {
			button:first-child {
				margin-inline-end: 2em;
			}
		}
		&--result {
			margin-bottom: 2em;
		}
	}
	hr {
		margin: 1.5em 0px;
	}
}
